<script setup>
import { ref, unref } from 'vue'
import Modal from './components/ModalWindow.vue'

const id = ref(1)
const newTodo = ref('')
const date = ref('')
const todos = ref([
  { id: id.value++, task: '歯医者に連絡する', deadline: `2023-07-01`, done: false },
  { id: id.value++, task: '映画を見にいく', deadline: '2023-07-03', done: false }
])

const showModal = ref(false)

function addTodo() {
  todos.value.push({ id: id.value++, task: newTodo.value, deadline: date.value, done: false })
  newTodo.value = ''
  date.value = ''
}

/**
 * @typedef {Object} Todo
 * @property {number} id
 * @property {string} task
 * @property {string} deadline
 * @property {boolean} done
 */
/** @type {import('vue').Ref<Todo>} */
const modalTodo = ref()
/**
 * @param {Todo} todo
 */
function clickedUpdateButton(todo) {
  showModal.value = true

  modalTodo.value = todo
}

/**
 * @param {number} modalId
 * @param {string} modalTask
 * @param {string} modalDeadline
 * @param {boolean} modalDone
 */
function updateTodo(modalId, modalTask, modalDeadline, modalDone) {
  todos.value.map((todo) => {
    if(todo.id === unref(modalId)) {
      todo.task = modalTask
      todo.deadline = modalDeadline
      todo.done = modalDone
    }
  })
}

function deleteTodo(modalId) {
  // modalIdとtodo.idが一致するオブジェクトを見つけて、その中身を全て書き換える
  const indexNumber = todos.value.findIndex((todo) => todo.id === unref(modalId))
  console.log(indexNumber)
  if (indexNumber !== -1) {
    todos.value.splice(indexNumber, 1)
  }
}
</script>

<template>
  <h1>ToDoApp</h1>

  <div class="form_area">
    <form @submit.prevent='addTodo'>
      <div class="form_input">
        <label>Task: </label>
        <input v-model='newTodo' placeholder="what's your task?">
      </div>
      <div class="form_input">
        <label>Deadline: </label>
        <input v-model='date' type=date>
      </div>
      <button>post</button>
    </form>
  </div>

  <hr />

  <div class='task_list'>
    <table>
      <thead>
      <tr>
        <th class='hidden'>ID</th>
        <th>Task</th>
        <th>Deadline</th>
        <th>Done</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for='todo in todos' :key='todo.id'>
        <td class='hidden'>{{ todo.id }}</td>
        <td>{{ todo.task }}</td>
        <td>{{ todo.deadline }}</td>
        <td>{{ todo.done ? '完了' : '未完了' }}</td>
        <td>
          <button @click='clickedUpdateButton(todo)'>更新</button>
        </td>
      </tr>
      </tbody>
    </table>
  </div>

  <!-- モーダル -->
  <div v-if='showModal'>
    <Teleport to='body'>
      <Modal :data="modalTodo"
             @close='showModal = false'
             @update="(
            /** @type {number} */ modalId,
            /** @type {string} */ modalTask,
            /** @type {string} */ modalDeadline,
            /** @type {boolean} */ modalDone) => {
            updateTodo(modalId, modalTask, modalDeadline, modalDone)
          }"
             @delete="(
            /** @type {number} */ modalId) => deleteTodo(modalId)"/>
    </Teleport>
  </div>
</template>

